import axios from 'axios'
import * as globalSelectors from '../global/globalSelectors'
import {
  busesAll,
  crewAll,
  scheduleError,
  scheduleLoader,
  scheduleMonth,
  passangersAll,
} from './scheduleActions'

axios.defaults.baseURL = 'https://car.busis.eu'

export const getScheduleMonth = (from, to) => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(scheduleLoader())
  try {
    const { data } = await axios(`/api/schedule?from=${from}&to=${to}`, {
      headers: { Authorization: auth },
    })
    dispatch(scheduleMonth(data))
  } catch (error) {
    dispatch(scheduleError(error))
  }
}

export const getCrewAll = () => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(scheduleLoader())
  try {
    const { data } = await axios(`/api/schedule/member/all`, {
      headers: { Authorization: auth },
    })
    dispatch(crewAll(data))
  } catch (error) {
    dispatch(scheduleError(error))
  }
}

export const getBusesAll = (departure, tripId) => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(scheduleLoader())
  try {
    const { data } = await axios(
      `api/schedule/vehicle/available?departure=${departure}&tripId=${tripId} `,
      {
        headers: { Authorization: auth },
      }
    )
    dispatch(busesAll(data))
  } catch (error) {
    dispatch(scheduleError(error))
  }
}

