import React, { useRef } from 'react'
import { CSSTransition } from 'react-transition-group'
import { AnimeBox } from '../stylesheets/mainStyled'

const Modal = ({ open, onClose, children }) => {
  const editRef = useRef(null)
  const handleBackdropClick = event => {
    const { current } = editRef
    if (current && event.target !== current) return
    onClose()
  }
  return (
    <CSSTransition
      in={open}
      timeout={300}
      classNames="scale"
      unmountOnExit
      nodeRef={editRef}
    >
      <AnimeBox ref={editRef} onClick={handleBackdropClick}>
        {children}
      </AnimeBox>
    </CSSTransition>
  )
}

export default Modal
