import { Types } from '../actionTypes'

export const vehiclesAll = data => ({ type: Types.GET_VEHICLES_ALL, payload: data })

export const vehicleId = data => ({ type: Types.GET_VEHICLE_ID, payload: data })

export const editVehicleId = data => ({ type: Types.EDIT_VEHICLE, payload: data })

export const addVehicleId = data => ({ type: Types.ADD_VEHICLE, payload: data })

export const removeVehicleId = id => ({ type: Types.REMOVE_VEHICLE, payload: id })

export const vehiclesError = err => ({ type: Types.VEHICLES_ERROR, payload: err })

export const vehiclesLoader = () => ({ type: Types.START_VEHICLES_LOADER })
