import React, { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { ReactComponent as ArrowBackIcon } from '../images/arrowBack.svg'
import AddVehicle from '../components/AddVehicle'
import ButtonAdd from '../components/ButtonAdd'
import Modal from '../components/Modal'
import { device } from '../stylesheets/device'
import { getBusesAll } from '../redux/schedule/scheduleOperations'
import { getbuses, getSchedule } from '../redux/schedule/scheduleSelectors'
import { useQuery } from '../services/useCustomQuery'
import moment from 'moment'
import Vehicle from '../components/ScheduleContainer/Vehicle'

const BusesPage = () => {
  const dispatch = useDispatch()
  const query = useQuery()

  const tripId = query.get('tripId')
  const departure = moment(query.get('departure')).format('YYYY-MM-DD')

  const buses = useSelector(getbuses)
  const trip = useSelector(getSchedule).find(
    trip =>
      trip.id === tripId &&
      trip.departure === moment(query.get('departure')).format('YYYY-MM-DD HH:mm')
  )
  const [isAdd, setIsAdd] = useState(false)
  const history = useHistory()

  const busesInfo = useCallback(
    (departure, tripId) => dispatch(getBusesAll(departure, tripId)),
    [dispatch]
  )
  useEffect(() => {
    busesInfo(departure, tripId)
  }, [busesInfo, departure, tripId])

  const handleAdd = () => setIsAdd(!isAdd)
  const toggleOpenModalAdd = () => setIsAdd(!isAdd)
  const goBack = () => history.push('/schedule')
  return (
    <>
      <Container>
        <ButtonAdd click={toggleOpenModalAdd} />
        <Header>
          <ArrowBackIcon onClick={goBack} />
          <Title>
            {moment(query.get('departure')).format('hh:mm')} {trip.name}
          </Title>
        </Header>
        <List>
          {buses.length > 0 &&
            buses.map(bus => <Vehicle key={bus.id} bus={bus} trip={trip} />)}
        </List>
      </Container>
      <Modal open={isAdd} onClose={toggleOpenModalAdd}>
        <AddVehicle onClose={handleAdd} />
      </Modal>
    </>
  )
}

export default BusesPage

const Container = styled.div`
  position: relative;
`

const Header = styled.header`
  display: flex;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
  padding: 12.4px 24px;
`
const Title = styled.p`
  font-size: 24px;
  padding-left: 12px;
`
const List = styled.ul`
  padding: 20px;
  margin: 0;
  @media ${device.tablet} {
    display: flex;
  }
`
