import { Types } from '../actionTypes'

export const scheduleMonth = data => ({ type: Types.GET_SCHEDULE, payload: data })

export const crewAll = data => ({ type: Types.GET_CREW, payload: data })

export const currentTrip = (trip, bus) => ({
  type: Types.CURRENT_TRIP,
  payload: { trip, bus },
})

export const busesAll = data => ({ type: Types.GET_BUSES, payload: data })

export const appointBus = id => ({ type: Types.APPOINT_BUS, payload: id })

export const removeBusId = id => ({ type: Types.DELETE_BUS, payload: id })

export const includeTrip = data => ({ type: Types.INCLUDE_TRIP, payload: data })

export const excludeTrip = data => ({ type: Types.EXCLUDE_TRIP, payload: data })

export const passangersAll = data => ({ type: Types.GET_PASSENGERS, payload: data })

export const scheduleLoader = () => ({ type: Types.SCHEDULE_LOADER_START })

export const scheduleError = data => ({ type: Types.SCHEDULE_ERROR, payload: data })
