import moment from 'moment'
import { createSelector } from 'reselect'

export const getSchedule = state => state.schedule.schedule
export const getCrew = state => state.schedule.crew
export const getCurrentTrip = state => state.schedule.currentTrip
export const getbuses = state => state.schedule.buses
export const getPassengers = state => state.schedule.passengers

export const getFormatSchedule = createSelector(getSchedule, day => {
  return day
    .sort((a, b) => {
      const A = new Date(a.departure).getTime()
      const B = new Date(b.departure).getTime()

      return A - B
    })
    .reduce((arr, el) => {
      if (!arr.some(i => i.date === moment(el.departure).format('YYYY-MM-DD'))) {
        arr.push({
          date: moment(el.departure).format('YYYY-MM-DD'),
          trips: day.reduce((acc, item) => {
            if (
              moment(item.departure).format('YYYY-MM-DD') ===
              moment(el.departure).format('YYYY-MM-DD')
            ) {
              acc.push(item)
            }
            return acc
          }, []),
        })
      }
      return arr
    }, [])
})
