import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { getAuth } from '../../redux/global/globalSelectors'
import { toAppointBus } from '../../services/apiMethods'
import { scheduleError } from '../../redux/schedule/scheduleActions'
import { Button } from '../../stylesheets/mainStyled'
import { device } from '../../stylesheets/device'

const Vehicle = ({ bus, trip }) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const auth = useSelector(getAuth)
  const toAppoint = async () => {
    try {
      const { status } = await toAppointBus(
        trip.id,
        trip.departure.slice(0, 10),
        bus.id,
        auth
      )
      if (status === 200) history.push('/scedule')
    } catch (error) {
      dispatch(scheduleError(error))
    }
  }
  return (
    <Card>
      <BusNumber>{bus.number}</BusNumber>
      <Text>{bus.model}</Text>
      <Text>
        Мест всего:<span>{bus.capacity}</span>
      </Text>
      {bus.hasOwnProperty('location') && (
        <Text>Последний раз находился в: {bus.location.name['RU']}</Text>
      )}
      {/*  */}
      <Button onClick={toAppoint}>Использовать</Button>
    </Card>
  )
}
export default Vehicle

const Card = styled.div`
  padding: 0 10px 20px 0;
  margin-bottom: 20px;
  border-bottom: ${prop => prop.theme.border};
  @media ${device.tablet} {
    border: ${prop => prop.theme.border};
    border-radius: 4px;
    padding: 12px;
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
    margin: 0 20px 20px 0;
  }
`

const BusNumber = styled.p`
  max-width: 140px;
  padding: 6px 20px;
  margin-bottom: 12px;
  border: ${prop => prop.theme.border};
  border-radius: 4px;
  font-size: 18px;
  background-color: #fff;
`
const Text = styled.p`
  font-size: 18px;
  margin-bottom: 6px;

  @media ${device.tablet} {
  }
`
