import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import { ReactComponent as CloseIcon } from '../images/close.svg'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import { device } from '../stylesheets/device'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import TextInput from './FormikInputs/TextInput'
import { ButtonRound } from '../stylesheets/mainStyled'
import { addVehicle } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'
import {
  addVehicleId,
  vehiclesError,
  vehiclesLoader,
} from '../redux/vehicles/vehiclesActions'
import Loading from './Loader'
import { getVehiclesError, getVehiclesLoader } from '../redux/vehicles/vehiclesSelectors'

const AddVehicle = ({ onClose }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const loader = useSelector(getVehiclesLoader)
  const error = useSelector(getVehiclesError)
  return (
    <Container>
      <Header>
        <Title>Новый автобус</Title>
        <CloseIcon onClick={onClose} />
      </Header>
      <Formik
        initialValues={{
          model: '',
          number: '',
          capacity: '',
        }}
        validationSchema={Yup.object({
          model: Yup.string().required('is required'),
          number: Yup.string().required('is required'),
          capacity: Yup.number().required('is required'),
        })}
        onSubmit={async ({ model, number, capacity }) => {
          const bus = { model, number, capacity }
          dispatch(vehiclesLoader())
          try {
            const { data } = await addVehicle(bus, auth)
            dispatch(addVehicleId(data))
            onClose()
          } catch (error) {
            dispatch(vehiclesError(error))
          }
        }}
      >
        <Form>
          <FormBox>
            <TextInput label="Название" name="model" type="text" />
            <TextInput label="Госномер" name="number" type="text" />
            <TextInput label="Вместительность" name="capacity" type="text" />
            {error && <TextError>error</TextError>}
            <ButtonRound type="submit">
              {loader ? (
                <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
              ) : (
                <CheckIcon />
              )}
            </ButtonRound>
          </FormBox>
        </Form>
      </Formik>
    </Container>
  )
}

export default AddVehicle

const Container = styled.div`
  background-color: #fff;
  height: 100%;
  width: 100%;
  position: relative;
  @media ${device.tablet} {
    width: 320px;
    margin: 0 auto;
  }
`
const Header = styled.div`
  padding: 12.4px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
`
const Title = styled.p`
  font-size: 24px;
`
const FormBox = styled.div`
  padding: 20px;
`
const TextError = styled.p`
  color: ${props => props.theme.warningColor};
`
